import { createStore } from 'vuex'

export default createStore({
  state: {
      words: [],
      show: [2, '*'], // % seccion de la tarjeta, % categoria
      categories: [],
      record: 0
  },
  mutations: {
      loadWords (state, file=null){
          if (file){
              state.words = [];
              state.categories = [];
              let allLines = file.split(/\r\n|\n/);
              let headers = allLines[0].split(',');
              allLines.shift();
              
              allLines.forEach(line => {
                  line = line.split(',');
                  let aux = {};
                  for(let i=0; i<headers.length; i++){
                      aux[headers[i]] = line[i];
                  }
                  state.words.push(aux);
                  if ( !state.categories.includes(line[0]) ) state.categories.push(line[0]);
              });
              state.words.pop(); // Elimina la ultima entrada vacia
              state.categories.pop(); // Elimina la ultima entrada vacia
              localStorage.setItem('words', JSON.stringify(state.words));
              localStorage.setItem('categories', JSON.stringify(state.categories));
          }
          else {
              state.words = JSON.parse( localStorage.getItem('words') ); 
              state.categories = JSON.parse( localStorage.getItem('categories') ); 
          }
      },
      addWord (state, word){
          state.words.push(word);
      },
      setShow (state, show){
          state.show = show;
      }
  },
  actions: {
  },
  modules: {
  }
})
